import Vue from 'vue'
import Router from 'vue-router'

import Menu from './components/Menu/index.vue';
import Error from "./components/Error/404.vue";

import Title from "./components/Menu/Title/Index.vue";
import ListNew from "./components/Menu/Product/ListNew.vue";
import NewProduct from "./components/Menu/Product/AddNewProduct.vue";
import InStockProduct from "./components/Menu/Product/AddInStock.vue";
import ListProduct from "./components/Menu/Product/Index.vue";


Vue.use(Router);

const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/gundamnew', component: Menu, meta: { text: 'Menu' }, children: [
                { path: '/gundamnew/title', component: Title, meta: { text: 'Tiêu đề đề' }, name: 'title' },
                { path: '/gundamnew/list-new', component: ListNew, meta: { text: 'Hàng Sắp Về' }, name: 'list-new-product' },
                { path: '/gundamnew/new-product', component: NewProduct, meta: { text: 'Thêm Mới Hàng Sắp Về' }, name: 'add-new-product' },
                { path: '/gundamnew/add-product', component: InStockProduct, meta: { text: 'Thêm Sản Phẩm' }, name: 'add-product' },
                { path: '/gundamnew', component: ListProduct, meta: { text: 'Danh Sách Sản Phẩm' }, name: 'list-product' },
            ]
        },
        { path: 'gundamnew/404', name: 'errorpage', component: Error },//token, user
        // { path: '*', redirect: '/404' }
    ]
})

export default router