<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="120px"
    class="demo-ruleForm"
  >
    <el-form-item label="Tựa đề" prop="title">
      <el-input v-model="ruleForm.title"></el-input>
    </el-form-item>
    <el-form-item class="overwrite-left">
      <el-button type="primary" @click="submitForm('ruleForm')">Lưu</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import Title from "../../../services/Title";
export default {
  name: "title",
  data() {
    return {
      ruleForm: {
        title: "",
      },

      rules: {
        title: [
          {
            required: true,
            message: "Vui lòng nhập tựa đề",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    onDataChange(items) {
      let _data = [];
      items.forEach((item) => {
        let data = item.val();
        _data.push({
          mess: data,
        });
      });
      this.ruleForm.title = _data[0].mess;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var data = { mess: this.ruleForm.title };

          console.log(data);

          Title.update("/", data)
            .then(() => {
              this.$notify({
                title: "Hoàn tất",
                message: "Lưu thành công",
                type: "success",
              });
            })
            .catch((e) => {
              this.$notify.error({
                title: "Lỗi",
                message: e,
              });
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  mounted() {
    Title.getAll().on("value", this.onDataChange);
  },
};
</script>

<style>
.overwrite-left {
  text-align: left;
}
</style>
