<template>
  <div>
    <el-row class="button-overwrite">
      <el-button type="primary" class="el-icon-refresh"> Reload</el-button>
      <el-button type="danger" class="el-icon-circle-close" @click="removeAll">
        Xóa hết</el-button
      >
    </el-row>
    <el-table :data="data" style="width: 100%" height="500">
      <el-table-column prop="id" label="ID" width="120"> </el-table-column>
      <el-table-column prop="name" label="Tên" width="120"> </el-table-column>
      <el-table-column prop="ImgUrl" label="Hình" width="300" align="center">
        <template slot-scope="scope">
          <img class="img" :src="scope.row.ImgUrl" alt="" />
        </template>
      </el-table-column>
      <el-table-column prop="price" label="Giá" width="120"> </el-table-column>
      <el-table-column prop="deposit" label="Đặt cọc"> </el-table-column>
      <el-table-column prop="status" label="Trạng thái">
        <template slot-scope="scope">
          <span>{{ scope.row.status ? "Còn hàng" : "Hết Hàng" }}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="Thao tác" width="200">
        <template slot-scope="scope">
          <el-button
            @click="prepareDelete(scope.row.id)"
            type="danger"
            icon="el-icon-delete"
            size="small"
            >Xóa</el-button
          >
          <el-button
            type="info"
            size="small"
            icon="el-icon-edit"
            @click="updateRow(scope.row)"
            >Sửa</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="Cảnh báo" :visible.sync="dialogDelete" width="30%">
      <span>Bạn có muốn xóa sản phẩm này?!</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogDelete = false">Không</el-button>
        <el-button type="danger" @click="handleDelete">Có</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Chỉnh sửa" :visible.sync="dialogEdit">
      <el-form :model="update" :rules="rules" ref="update">
        <el-form-item label="Tên" prop="name" :label-width="formLabelWidth">
          <el-input v-model="update.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="Giá" prop="price" :label-width="formLabelWidth">
          <el-input v-model.number="update.price" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item
          label="Giá cọc"
          prop="deposit"
          :label-width="formLabelWidth"
        >
          <el-input
            v-model.number="update.deposit"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="Hình ảnh" :label-width="formLabelWidth">
          <div class="wrapper-img">
            <img v-if="!image_upload" src="./../../../assets/empty-img.png" />
            <img v-else :src="image_upload" alt="" />
            <input
              accept="image/jpeg"
              type="file"
              class="file-upload"
              id="fileupload-slide"
              ref="upload"
              @change="showImg"
            />
          </div>
        </el-form-item>

        <el-form-item
          label="Trạng thái"
          prop="status"
          :label-width="formLabelWidth"
        >
          <el-switch v-model="update.status"></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogEdit = false">Hủy Bỏ</el-button>
        <el-button type="primary" @click="edit">Ghi</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Gundam from "../../../services/Gundam";
import { storage } from "../../../storage";
export default {
  name: "tutorials-list",
  data() {
    return {
      data: [],
      dialogDelete: false,
      deleteItem: null,
      dialogEdit: false,
      image_upload: "",
      update: {
        ImgUrl: "",
        deposit: null,
        id: null,
        name: "",
        price: null,
        status: true,
      },
      rules: {
        name: [
          {
            required: true,
            message: "Vui lòng nhập tên sản phẩm",
            trigger: "blur",
          },
        ],
        price: [
          { required: true, message: "Vui lòng nhập giá" },
          { type: "number", message: "Vui lòng nhập số" },
        ],
        deposit: [
          { required: true, message: "Vui lòng nhập giá" },
          { type: "number", message: "Vui lòng nhập số" },
        ],
      },
      formLabelWidth: "120px",
    };
  },
  methods: {
    handleDelete() {
      if (this.deleteItem == null) {
        return;
      }
      this.dialogDelete = true;
      Gundam.delete(this.deleteItem)
        .then(() => {
          this.dialogDelete = false;
          this.$notify({
            title: "Hoàn tất",
            message: "Xóa thành công",
            type: "success",
          });
          this.getAll();
          this.deleteItem = null;
        })
        .catch((e) => {
          this.$notify.error({
            title: "Lỗi",
            message: e,
          });
        });
    },
    prepareDelete(key) {
      this.dialogDelete = true;
      this.deleteItem = key;
    },
    onDataChange(items) {
      let _tutorials = [];

      items.forEach((item) => {
        let data = item.val();
        _tutorials.push({
          ImgUrl: data.ImgUrl,
          deposit: data.deposit,
          id: data.id,
          name: data.name,
          price: data.price,
          status: data.status,
        });
      });

      this.data = _tutorials;
    },
    async edit() {
      if (!this.$refs.upload.files[0] || !this.image_upload) {
        this.$notify.error({
          title: "Lỗi",
          message: "Chưa nhập hình",
        });

        return;
      }
      await this.uploadImage();
      await this.$refs["update"].validate((valid) => {
        if (valid) {
          Gundam.update(this.update.id, this.update)
            .then(() => {
              this.dialogEdit = false;
              this.$notify({
                title: "Hoàn tất",
                message: "Sửa thành công",
                type: "success",
              });
            })
            .catch((e) => {
              this.$notify.error({
                title: "Lỗi",
                message: e,
              });
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    updateRow(row) {
      this.update.deposit = row.deposit;
      this.update.id = row.id;
      this.update.name = row.name;
      this.update.price = row.price;
      this.update.status = row.status;
      this.update.ImgUrl = row.ImgUrl;
      this.image_upload = row.ImgUrl;
      this.dialogEdit = true;
    },
    removeAll() {
      Gundam.deleteAll()
        .then(() => {
          this.$notify({
            title: "Hoàn tất",
            message: "Xóa thành công",
            type: "success",
          });
          this.getAll();
        })
        .catch((e) => {
          this.$notify.error({
            title: "Lỗi",
            message: e,
          });
        });
    },
    showImg(e) {
      this.image_upload = URL.createObjectURL(e.target.files[0]);
    },
    async uploadImage() {
      let ImgToUpload = this.$refs.upload.files[0];
      const metaData = {
        contentType: ImgToUpload.type,
      };
      const storageRef = storage.ref(`Images/`);
      const imageRef = storageRef.child(ImgToUpload.name);

      await imageRef.put(ImgToUpload, metaData);

      const downloadURL = await imageRef.getDownloadURL();
      this.update.ImgUrl = downloadURL;
    },
    getAll() {
      Gundam.getAll().on("value", this.onDataChange);
    },
  },
  mounted() {
    this.getAll();
  },
  beforeDestroy() {
    Gundam.getAll().off("value", this.onDataChange);
  },
};
</script>

<style>
.img {
  width: 150px;
  height: 150px;
}

.button-overwrite {
  margin: 15px 0px;
  text-align: left;
}

.wrappe-image {
  height: inherit;
}
.wrapper-img {
  height: 200px;
  width: 200px;
  position: relative;
  border: 2px dashed #d9d9d9;
  border-radius: 5px;
  overflow: hidden;
  z-index: 1;
}

.wrapper-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wrapper-img .default {
  position: absolute;
  top: -50%;
  left: -50%;
  transform: translate(-50%, -50%);
  background: #fff;
}

.file-upload {
  position: absolute;
  bottom: 0px;
  outline: none;
  color: transparent;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.5s;
  z-index: 2;
  display: block;
}

.file-upload::-webkit-file-upload-button {
  visibility: hidden;
}
</style>
